.card {
  background: #323dcf;
  border-radius: 3px;
  width: 200px;
  height: 200px;
  margin: 1rem;
  position: relative;
  -webkit-box-shadow: 0 3px 6px #000, 0 3px 6px #000;
          box-shadow: 0 3px 6px #000, 0 3px 6px #000;
  -webkit-transition: -webkit-transform .2s;
  transition: -webkit-transform .2s;
  -o-transition: transform .2s;
  transition: transform .2s;
  transition: transform .2s, -webkit-transform .2s;
}

.card:hover {
 
  -webkit-box-shadow: 0 0 20px #ccc, 0 0 20px #ccc;
 
          box-shadow: 0 0 20px #ccc, 0 0 20px #ccc;
  -webkit-transform: scale(1.05);
      -ms-transform: scale(1.05);
          transform: scale(1.05);
}

.flexContainer {
  padding: 10px;
  margin: 6px;
  list-style: none;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.flexItem {
  padding: 5px;
  margin-top: 10px; 
}

.card > .content {
  padding-left: 1rem;
  padding-right: 1rem;
}


/* =======MEDIA QUERIES====== */

/* reduce card & image size a bit for laptop & tablets */
@media (max-width: 1200px) {
  .tformerImg {
    width: 120px;
    height: 120px;
  }
  .card {
    width: 120px;
    height: 120px;
  }
}


@media (max-width: 767px) {
  /* remove hover on mobile */
  .card:hover {
     cursor: none;
     -webkit-box-shadow: 0 3px 6px #000, 0 3px 6px #000;
             box-shadow: 0 3px 6px #000, 0 3px 6px #000;
     -webkit-transform: scale(1);
         -ms-transform: scale(1);
             transform: scale(1);
  }
  /* reduce card size for mobile */
   .card {
    width: 65px;
    height: 65px;
  }
  /* reduce image size for mobile */
  .tformerImg {
    width: 65px;
    height: 65px;
  }
}












